* {
	box-sizing: border-box;
}

body, html {
  	color: $text-color;
  	font-family: $text-font-stack;
	margin: 0;
	padding: 0;
	width: 100%;
	font-style: normal;
	font-weight: $font-weight-light;
	font-size: 16px;
	line-height: 1.425;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-display: optional;
	position: relative;
	min-height: 100%;
	background-color: $bg-color;
	@include laptop {
		font-size: 14.5px;
	}
	@include tablet {
		font-size: 14px;
	}
	@include tablet_610 {
		font-size: 13.6px;
	}
	@include mobile {
		font-size: 13px;
	}
	@include s-mobile {
		font-size: 12px;
	}
}

*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: $primary-color;
  text-decoration: none;
}

ul {
	margin: 0;
	padding: 0;
}

ul li {
	list-style: none;
}

h1, h2, h3, p {
	margin: 0;
}

svg {
	fill: currentColor;
	stroke: currentColor;
}

button, input, select, textarea {
  font-family: $text-font-stack;
 }

.wrapper {
	@include size(100%);
	margin: 0;
	position: relative;
	min-height: 100vh;
	@include custom_1800 {
		width: 1800px;
		margin: 0 auto;
	}
	@include custom {
		min-height: 650px;
	}
}

.container {
	min-width: 80%;
	width: 1200px;
	height: 100%;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	align-items: center;
	@include custom {
		width: 93.75%;
	}
	@include desktop {
		width: 93.75%;
	}
	@include laptop {
		width: 93.75%;
	}
	@include tablet {
		width: 93.75%;
	}
	&.d-bl {
		display: block;
	}
	&.ai-bsln {
		align-items: baseline;
	}
	@include tablet {
		flex-wrap: wrap;
	}
}

.section {
	width: 100%;
	overflow: hidden;
}


