body.thank {
	background: rgb(110,203,218);
background: -webkit-linear-gradient(rgba(110,203,218,1) 0%, rgba(26,147,166,1) 100%);
background: -o-linear-gradient(rgba(110,203,218,1) 0%, rgba(26,147,166,1) 100%);
background: linear-gradient(rgba(110,203,218,1) 0%, rgba(26,147,166,1) 100%);
}
.thank__wr {
	@include size(80%, 100vh);
	margin: 0 auto;
	position: relative;
	min-height: 100vh;
	overflow: hidden;
	@include custom_1800 {
		width: 1000px;
		margin: 0 auto;
	}
	@include custom {
		min-height: 650px;
	}
}

.main-title_thank {
	margin-top: 7%;
    margin-bottom: rem(49);
    font-size: rem(42);
    @include s-mobile {
    	margin-top: 3%;
    	margin-bottom: rem(16);
    }
}

.card-title_thank {
	font-size: rem(33);
	@include s-mobile {
		margin-bottom: rem(16);
	}
}

.thank__text {
	font-size: rem(30);
	font-weight: $font-weight-light;
	color: $pink-color;
	margin: rem(32) 0 rem(19) 0;
	text-align: left;
}

.about__submit-button_thank {
	display: block;
	width: rem(400);
	margin-left: auto;
	@include tablet_610 {
		width: 80%;
	}
	@include s-mobile {
		width: 100%;
	}
}


