.main-title {
	font-size: rem(48);
	font-weight: $font-weight-bold;
	text-align: center;
	color: $primary-color;
	line-height: 1.34;
    margin-bottom: $main-title-mb;
    vertical-align: text-top;
	@include laptop {
		font-size: rem(40);
	}
	@include tablet {
		font-size: rem(40);
	}
	@include tablet_610 {
		font-size: 1.85rem;
	}
	@include s-mobile {
		font-size: 1.7rem;
	}
	&.bg-blue {
		background-color: $blue-color;
	}
	&.c-pink {
		color: $pink-color;
		margin-bottom: rem(70);
		@include tablet {
			margin-bottom: rem(54);
		}
	}
	& mark {
		background-color: $blue-color;
		color: $primary-color;
	}
}

.card-title {
	font-size: rem(30);
	font-weight: $font-weight-light;
	text-align: center;
	color: $text-color;
	margin-bottom: rem(90);
	&.c-white {
		font-weight: $font-weight-bold;
		color: $primary-color;
		margin-bottom: rem(55);
		@include tablet {
			margin-bottom: 3.2rem;
		}
	}
	&.c-pink {
		font-weight: $font-weight-bold;
		color: $pink-color;
		margin-bottom: rem(55);
	}
	&.mb-2-4rem {
		margin-bottom: 2.4rem;
	}
	&.mb-3rem {
		margin-bottom: 3rem;
	}
	@include tablet {
		margin-bottom: 4rem;
	}
}


