.about {
	min-height: rem(820);
	background: rgb(110,203,220);
	background: -webkit-linear-gradient(180deg, rgba(110,203,220,1) 0%, rgba(27,146,166,1) 100%);
background: -o-linear-gradient(180deg, rgba(110,203,220,1) 0%, rgba(27,146,166,1) 100%);
background: linear-gradient(180deg, rgba(110,203,220,1) 0%, rgba(27,146,166,1) 100%);
	padding: rem(30) 0 rem(15) 0;
}

.about__col-one {
	width: 50%;
	height: 100%;
	padding-right: 1.5%;
	@include tablet {
		width: 100%;
		padding: 0;
		margin-bottom: 1.7rem;
	}
}

.about__contacts {
	font-size: rem(22);
	font-weight: $font-weight-light;
	text-align: left;
	color: $primary-color;
	margin-bottom: rem(15);
}

.about__address {
	margin-bottom: 1.8rem;
	@include tablet {
		margin-bottom: 1rem;
	}
}

.about__address-link {
	line-height: 1.5;
	transition: color 0.3s;
	&:hover, &:active, &:focus {
		color: $dark-blue-color;
	}
}

.about__phone {
	&:hover .about__phone-link, &:active .about__phone-link, &:focus .about__phone-link {
		color: $dark-blue-color;
	}
	& span {
		vertical-align: bottom;
	}
}

.about__phone-link {
	padding-left: rem(8);
	vertical-align: bottom;
	transition: color 0.3s;
}

.about__img-wr {
	width: 100%;
	height: auto;
	overflow: hidden;
	margin-bottom: rem(15);
	@include tablet {
		margin-bottom: 1.7rem;
	}
}

.about__img {
	max-width: 100%;
	height: auto;
}

.about__content {
	font-size: rem(22);
	font-weight: $font-weight-light;
	text-align: left;
	color: $primary-color;
	& p {
		margin-bottom: rem(9);
	}
}

.about__col-two {
	width: 50%;
	height: 100%;
	padding-left: 1.5%;
	@include tablet {
		width: 100%;
		padding: 0;
		margin-bottom: 1.7rem;
	}
}

.about-form__inner {
	width: 100%;
	@include tablet {
		width: 95%;
		margin: 0 auto;
	}
}

.about-form__row {
	position: relative;
	width: 100%;
	margin-bottom: rem(12);
}

.about-form__input {
	width: 100%;
	height: rem(50);
	border-radius: rem(25);
	background-color: rgba($primary-color, 0.85);
	border: 0.5px solid $blue-color;
    padding: rem(4) rem(10);
    outline: none;
    text-align: center;
    overflow: hidden;
    transition: border 0.2s, box-shadow 0.2s;
    font-size: rem(30);
    color: rgba($text-color, 0.85);
	font-weight: $font-weight-light;
	font-style: normal;
	vertical-align: middle;
	@include laptop {
		font-size: rem(20);
		@include placeholder {
			font-size: rem(20);
		}
	}
    @include placeholder {
		font-size: rem(30);
		font-weight: $font-weight-light;
		font-style: normal;
		text-align: center;
		color: rgba($text-color, 0.85);
	}
	&:hover, &:focus {
		cursor: pointer;
		box-shadow: inset 0px 0px 3.5px 0.2px $blue-color;
	}
}

textarea.about-form__input {
	resize: none;
	height: rem(246);
	&.pt-13 {
		padding-top: rem(13);
	}
}

.about__submit {
	display: inline-block;
	text-align: center;
	width: 100%;
	margin-top: rem(7);
	@include tablet {
		margin-top: 0.8rem;
	}
}

.about__submit-button {
	width: 100%;
	height: rem(50);
	border-radius: rem(25);
	background-color: rgba($pink-color, 0.85);
	border: solid 1px rgba($pink-color, 1);
	outline: none;
	overflow: hidden;
	text-align: center;
	box-shadow: none;
	transition: background-color 0.25s, border 0.25s;
	box-shadow: none;
	&:hover, &:focus, &:active {
		cursor: pointer;
		background-color: rgba($pink-color, 1);
		border: solid 1px rgba($pink-color, 1);
		box-shadow: inset 0px 0px 5px 0.1px $pink-color;
	}
	& span {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		vertical-align: middle;
		font-size: rem(24);
		font-weight: $font-weight-light;
		font-style: normal;
		line-height: rem(50);
		text-align: center;
		color: $primary-color;
		transition: color 0.25s;
		@include custom {
			line-height: rem(60);
		}
	}
	&:disabled {
		opacity: 0.7;
    	cursor: initial;
	}
	@include custom {
		height: rem(60);
	}
	@include tablet {
		width: 50%;
		margin: 0 auto;
	}
	@include mobile {
		width: 80%;
		margin: 0 auto;
	}
	&.w-100 {
		@include tablet {
			width: 100%;
			margin: 0;
		}
		@include mobile {
			width: 100%;
			margin: 0;
		}
	}
}


